import React, {useState} from "react";
import styles from './Header.module.css'
import MenuBtn from "../Other/MenuBtn/MenuBtn";
import logo from '../assets/icons/Logo.png';
import arrowDown from '../assets/icons/Arrow Down.png';
import arrow from '../assets/icons/Arrow.png';


function Header() {

    return <div className={styles.header}>
        <div className={styles.menuBlock}>

            <div className={styles.logoWrapper}>
                <img className={styles.logo} src={logo} alt=""/>
            </div>


            <div className={styles.leftNavBar}>
                <div className={styles.menuBtn} ><MenuBtn />
                {/*<div className={styles.hiddenContainer}></div>*/}
                </div>
                <ul>
                    <li><a href="">Home.</a></li>
                    <li><a href="">About Us.</a></li>
                    <li><a href="">Portfolio.</a></li>
                    <li><a href="">Blog.</a></li>
                    <li><a href="">Contact us.</a></li>
                </ul>
            </div>


                <div className={styles.rightNavBar}>
                    <div className={styles.localization}>EN<img src={arrowDown} alt=""/></div>
                    <div><button className={styles.button}>Contact Us<img src={arrow} alt=""/></button></div>
                </div>

        </div>
    </div>
}

export default Header;