import React from "react";
import styles from './AboutUs.module.css'
import SlickSlider from "../Other/SlickSlider/SlickSlider";

const AboutUs = () => {
    return <div className={styles.aboutUs}>
        <div className={styles.title}>
            <div className={styles.slogan}>The Spirit of <br/>Digital Agency.</div>
            <div className={styles.text} align="justify">Vestibulum ac diam sit amet quam vehicula elementum amet est on dui. Nulla porttitor accumsan tincidunt.</div>
            <div className={styles.container}>
                <div className={styles.moreAboutUs}><button className={styles.moreAboutUsBtn}>More About Us</button></div>
                <div >Get in Touch.</div>
            </div>

        </div>
        <div className={styles.illustration}>
            <SlickSlider/>
        </div>
    </div>
}

export default AboutUs;