import React from "react";
import styles from './Footer.module.css';
import facebookImg from '../assets/icons/Facebook.png';
import twitterImg from '../assets/icons/Twitter.png';
import dribbleImg from '../assets/icons/Dribble.png';

const Footer = () => {
    return <div className={styles.footerWrapper}>
        <div className={styles.footer}>
            <h5><span>DOOB</span></h5>
            <h2><span>CREATIVITY ABOVE</span></h2>
            <nav className={styles.footerNavBar}>
                <ul>
                    <li><a href="">Home.</a></li>
                    <li><a href="">About Us.</a></li>
                    <li><a href="">Portfolio.</a></li>
                    <li><a href="">Blog.</a></li>
                    <li><a href="">Contact us.</a></li>
                </ul>
            </nav>
            <p> © 2019 - Doob,All Right Reserved</p>
            <div className={styles.social}>
                <a href=""><img src={facebookImg} alt=""/></a>
                <a href=""><img src={twitterImg} alt=""/></a>
                <a href=""><img src={dribbleImg} alt=""/></a>
            </div>
        </div>
    </div>
}

export default Footer;