import React from "react";
import styles from './Works.module.css'
import WorkExample from '../Other/WorkExample/WorkExample'

class Works extends React.Component {
    render() {
        return <div className={styles.worksWrapper}>
            <div className={styles.leftBlock}/>
            <div className={styles.works}>
                <div className={styles.worksTitle}>
                    See some of our <br/>
                    Creative work.
                </div>
                <div className={styles.worksExamples}>
                    <WorkExample/>
                    <WorkExample/>
                    <WorkExample/>
                    <WorkExample/>
                    <WorkExample/>
                    <WorkExample/>
                </div>
                <div className={styles.seeMoreText}>
                    <strong>See More of These.</strong>
                </div>
            </div>

        </div>
    }
}

export default Works;